@import "../../Utils";

.Comment {
  border-left: 1px solid $colorLight;
  padding-left: 15px;
  margin-bottom: 30px;
  &:last-child {
    margin-bottom: 0;
  }

  &__title {
    display: inline-block;
    margin: 0;
    margin-right: 20px;
    font-size: 18px;
  }

  &__email {
    display: inline-block;
    margin: 0;
  }
}
